.App {
  text-align: center;
  margin-top: 30px;
  
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.App1, .App2 {
  display:flex;
  gap:2rem;
  margin: 15px;
}

.heading1, .heading4 {
  background-color: antiquewhite;
  color:green
  
  
}

.heading2, .heading3 {
  background-color: gainsboro;
  color:red
  
}
